import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { ProductServiceService } from '../services/product-service.service';


@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {

  @Input() categoryID: string;
  @Input() products: Array<{}>;
  @Input() isAdmin: boolean;
  @Output() wasChange = new EventEmitter();
  @Output() addProductToCart = new EventEmitter();

  productName: string;
  price: number;
  img: string;
  
  constructor( private productService: ProductServiceService ) { }

  addProduct(){    
    const productName = this.productName;
    const categoryID = this.categoryID;
    const price = this.price;
    const img = this.img;
    this.productService.addNewProduct({productName, categoryID, price, img}).subscribe((res)=>{
      this.productName = '';
      this.price = null;
      this.img = '';
      this.wasChange.emit();
    })
  }

  changesFromChild(){
    this.wasChange.emit()
  }

  addToCart($event){
    this.addProductToCart.emit($event);
  }

  ngOnInit(): void {
  }

  

}
