import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { AuthServiceService } from '../services/auth-service.service';
import { CartSrviceService } from '../services/cart-srvice.service';
import { CategoryServiceService } from '../services/category-service.service';
import { ProductServiceService } from '../services/product-service.service';


@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  categories: Array<{_id:number, categoryName:string}>;
  categoryID: string;
  products:any [];
  isAdmin:boolean;
  user: any;
  cart: any;
  public addItemTocart: EventEmitter<boolean> = new EventEmitter();
  

  constructor( 
    private router: Router,
    private auth: AuthServiceService,
    private categoryService: CategoryServiceService,
    private productService: ProductServiceService,
    private cartService: CartSrviceService
    ) { }
  
  ngOnInit(): void {
    this.auth.getSession().subscribe( (res: any) => {
      if(!res.passport){
        this.moveToLogin()
      }
      else{
        this.user = res.passport.user;
        if(this.user.role === "admin"){
          this.isAdmin = true
          this.router.navigate(['admin']);
        }else{
          this.isAdmin = false
        }
      }
    });
    this.categoryService.getCategories().subscribe( (res: any) => { this.categories = res } );
    this.productService.getAllProduct().subscribe( (res: any ) => { this.products = res } );
    this.cartService.getCart().subscribe((res)=>{ 
      this.cart = res[0];
      console.log("**************CART*********************")
      console.log(this.cart);
      console.log("**************CART*********************")
     })
  }


  selectCategory($event){
    this.categoryID = $event;
    this.getAllProductsByCategoryID();
  }

  getAllProductsByCategoryID(){
    this.productService.getAllProductsByCategoryID(this.categoryID).subscribe((response: Array<{}>)=>{
      this.products = response;
    })
  }

  moveToLogin(){
    this.router.navigate(['/login'])
  }
  wasChange(){
    this.getAllProductsByCategoryID();
  }

  addProductToCart($event){
    console.log($event);
    let item = {
      productID: $event._id,
      cartID: this.cart._id,
      count: 1,
      totalPrice: $event.price,
      productName: $event.productName
    }
    this.cartService.addCartItem(item).subscribe((res)=>{
      this.addItemTocart.emit(true)
     })
  }

 
}
