import { Component, Input, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { AuthServiceService } from '../services/auth-service.service';
import { CartSrviceService } from '../services/cart-srvice.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {
  @Input() wasChange: EventEmitter<boolean>;

  cart: any;
  cartItems: any;
  showCart: boolean = false;
  user: any;
  totalPriceForAllCart: number;

  constructor(
    private router: Router,
    private cartService: CartSrviceService,
    private auth: AuthServiceService
    ) { }

  ngOnInit(): void { 
    this.auth.getSession().subscribe((res: any)=>{ this.user = res.passport.user })
    this.getCart();
    this.wasChange.subscribe((res)=>{
      this.getCart();
    })
  }

  getCart(){
    this.cartService.getCart().subscribe((res)=>{
      this.cart = res;
      this.cartController();
    });
  }

  cartController(){
   if(this.cart){
    if( this.cart.length == 0){
      this.cartService.addNewCart(this.user._id).subscribe( (res: any) => { 
        this.cart = res;
        return
      })
    }else{
      this.cartService.getAllCartItems(this.cart[0]._id).subscribe((res)=>{
        
        this.cartItems = res;
        console.log(res)
        this.getTotalPriceForAllCart()
      })
    }
   }
  }

  toggle(){
    this.showCart = !this.showCart;
  }

  getTotalPriceForAllCart(){
    this.totalPriceForAllCart = this.sum("totalPrice").toFixed(2);

  }

  sum(key) {
    return this.cartItems.reduce((a, b) => a + (b[key] || 0), 0);
  }

  addItem(cartItem){
    console.log(cartItem);
    this.cartService.addCartItem(cartItem).subscribe((res)=>{
      this.cartService.getAllCartItems(cartItem.cartID).subscribe((res)=>{
        this.cartItems = res
      })
    })
  }


  decrementItem(cartItem){
    this.cartService.decrementItemCount(cartItem).subscribe((res)=>{
      this.cartService.getAllCartItems(cartItem.cartID).subscribe((res)=>{
        this.cartItems = res
      })
    })
  }

  moveToOrder(){
    this.router.navigate(['order'])
  }
}
