import { Component, OnInit } from '@angular/core';
import { CategoryServiceService } from '../services/category-service.service';
import { ProductServiceService } from '../services/product-service.service';
import { AuthServiceService } from '../services/auth-service.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {

  categories: Array<{_id:number, categoryName:string}>;
  categoryID: string;
  products:any [];
  isAdmin:boolean = true;
  user: any
  



  constructor(
    private categoryService: CategoryServiceService,
    private productService: ProductServiceService,
    private router: Router,
    private auth: AuthServiceService
    ) { }

  ngOnInit(): void {
    this.categoryService.getCategories().subscribe((response:Array<{_id:number, categoryName:string}> )=>{
      this.categories = response;
    });
    this.auth.getSession().subscribe( (res: any) => {
      if(!res.passport){
        this.moveToLogin()
      }
      else{
        this.user = res.passport.user;
        if(this.user.role === "admin"){
          this.router.navigate(['admin']);
        }
        else{
          this.moveToLogin()
        }
      }
    })
  }
  
  moveToLogin(){
    this.router.navigate(['/login'])
  }

  selectCategory($event){
    this.categoryID = $event;
    console.log(this.categoryID);
    this.getAllProductsByCategoryID();
  }

  getAllProductsByCategoryID(){
    this.productService.getAllProductsByCategoryID(this.categoryID).subscribe((response: Array<{}>)=>{
      this.products = response;
      console.log(this.products);
    })
  }

  wasChange(){
    this.getAllProductsByCategoryID();
  }
}
