import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ProductServiceService } from '../services/product-service.service';

@Component({
  selector: 'app-product-item',
  templateUrl: './product-item.component.html',
  styleUrls: ['./product-item.component.css']
})
export class ProductItemComponent implements OnInit {
  @Input() product: { img: string, price: number, productName: string, _id: string };
  @Input() isAdmin: boolean
  isEditMode:boolean = false;

  @Input() categoryID: string;
  productName: string = '';
  price: number;
  img: string = '';

  @Output() wasChange = new EventEmitter();
  @Output() addProductToCart = new EventEmitter();

  constructor(private productService: ProductServiceService) { }

  ngOnInit(): void {
  }

  editModeToggle(){
    this.isEditMode = !this.isEditMode;
  }

  saveChanges(){
    const params = {
      productName: this.productName, 
      price: this.price,
      img: this.img
    }
    this.productService.updateProduct(this.product._id, params).subscribe((res)=>{
      this.wasChange.emit();
      this.editModeToggle();
    })
  }

  addToCart(){
    // console.log(this.product._id)
    this.addProductToCart.emit(this.product)
  }

}
