import {
  Router
} from '@angular/router';
import {
  Component,
  OnInit
} from '@angular/core';
import {
  AuthServiceService
} from '../services/auth-service.service';
import {
  CartSrviceService
} from '../services/cart-srvice.service';
import * as html2pdf from 'html2pdf.js';
import {
  BillServiceService
} from '../services/bill-service.service';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {
  
  error: boolean = false;
  cityError: boolean = false;
  streetError: boolean = false;
  shippingDateError: boolean = false;
  viewOrderToggle: boolean = true;
  totalPriceForAllCart: number;
  cart: any;
  cartItems: any;
  creditCard: any;
  city: string;
  street: string;
  shippingDate: Date;
  user: any;
  successOrder: boolean = false;

  constructor(
    private router: Router,
    private cartService: CartSrviceService,
    private auth: AuthServiceService,
    private billService: BillServiceService
  ) {}

  ngOnInit(): void {
    this.auth.getSession().subscribe((res: any) => {
      if (!res.passport) {
        this.router.navigate(['login']);
      } else {
        this.user = res.passport.user;
          if(this.user.role === "admin"){
            this.router.navigate(['admin'])
          }
        this.cartService.getCart().subscribe((res) => {
          this.cart = res;
          this.cartService.getAllCartItems(this.cart[0]._id).subscribe((res) => {
            this.cartItems = res;
            this.getTotalPriceForAllCart();
          })
        })
      }
    })
  }


  getTotalPriceForAllCart() {
    this.totalPriceForAllCart = this.sum("totalPrice").toFixed(2);

  }

  sum(key) {
    return this.cartItems.reduce((a, b) => a + (b[key] || 0), 0);
  }



  backToOrder() {
    this.viewOrderToggle = true;
  }

  confirmOrder() {
    this.viewOrderToggle = false;
  }



  submitOrder() {
    if (this.ValidateCreditCardNumber() && this.validateCity() && this.validateStreet() && this.validateShippingDate()) {
      let bill = {
        clientID: this.user._id,
        cartID: this.cart[0]._id,
        totalPrice: this.totalPriceForAllCart,
        city: this.city,
        street: this.street,
        shippingDate: this.shippingDate,
        lastCreditNumber: parseInt(this.creditCard.toString().slice(-4))
      }
      this.billService.addBill(bill).subscribe((res: any)=>{
        //***************************************** */
        console.log(res);
        if(res.shippingDateError){
          this.shippingDateError = true;
        }else{
          // this.exportToPDF();
          this.successOrder = true;
        }
      })
    } else {
      this.creditCardError()
    }
  };

  exportToPDF() {
    const options = {
      filename: 'Bill',
      image: {
        type: 'jpeg'
      },
      html2canvas: {},
      jsPDF: {
        orientation: 'landscape'
      }
    }

    const content: Element = document.getElementById('exportToPdfFile');
    html2pdf()
      .from(content)
      .set(options)
      .save();
      this.resetInputs();
      this.successOrder = false;
      this.router.navigate([''])
  }

  creditCardError() {
    this.error = true;
  }


  validateCity() {
    let isValid = false;
    let regexString = /^[a-zA-Z]+$/;
    if (regexString.test(this.city)) {
      isValid = true;
      this.cityError = isValid
    }
    this.cityError = isValid;
    return isValid;
  }

  validateStreet() {
    let isValid = false;
    let regexString = /^[a-zA-Z]+$/;
    if (regexString.test(this.street)) {
      isValid = true;
      this.streetError = isValid;
    }
    this.streetError = isValid;
    return isValid;
  }




  validateShippingDate() {
    let isValid = false;
    if (this.isValidDate(this.shippingDate)) {
      isValid = true;
      this.shippingDateError = !isValid;
    }
    this.shippingDateError = !isValid;
    return isValid
  }

  isValidDate(dateString) {
    var regEx = /^\d{4}-\d{2}-\d{2}$/;
    if (!dateString.match(regEx)) return false; // Invalid format
    var d = new Date(dateString);
    var dNum = d.getTime();
    if (!dNum && dNum !== 0) return false; // NaN value, Invalid date
    return d.toISOString().slice(0, 10) === dateString;
  }

  ValidateCreditCardNumber() {
    let ccNum = this.creditCard;
    let visaRegEx = /^(?:4[0-9]{12}(?:[0-9]{3})?)$/;
    let mastercardRegEx = /^(?:5[1-5][0-9]{14})$/;
    let amexpRegEx = /^(?:3[47][0-9]{13})$/;
    let discovRegEx = /^(?:6(?:011|5[0-9][0-9])[0-9]{12})$/;
    let isValid = false;

    if (visaRegEx.test(ccNum)) {
      isValid = true;
    } else if (mastercardRegEx.test(ccNum)) {
      isValid = true;
    } else if (amexpRegEx.test(ccNum)) {
      isValid = true;
    } else if (discovRegEx.test(ccNum)) {
      isValid = true;
    }

    if (isValid) {
      return true
    } else {
      return false
    }
  }


  closeSuccessOrder(){
    this.successOrder = false;
    this.resetInputs();
  }

  resetInputs(){
    this.creditCard = null;
    this.city = '';
    this.street = '';
    this.shippingDate = null
  }


  removeError() {
    this.error = false;
  }
}
